export default {
	data() {
		return {
			addressId: '',
			ids: '',
			id: '',
			addressInfo: '',
			lists: []
		};
	},
	onLoad(options) {
		if (options.parms) {
			this.ids = decodeURI(options.parms);
		}
		if(options.speId){
			this.speId = options.speId;
		}
		
	},
	onShow() {
		this.getDetail();
	},
	methods: {
		getDetail() {
			let data;
			if (uni.getStorageSync('addressId')) {
				this.addressId = uni.getStorageSync('addressId');
				try {
					uni.removeStorageSync('addressId');
				} catch (e) {
					// error
				}
			}
			if (this.ids) {
				data = {
					ids: this.ids,
					addressId: this.addressId
				}
			}else{
				data = {
					id: this.speId,
					addressId: this.addressId
				}
			}
			this.$http.request({
				url: this.$api.AppointOrders,
				data: data
			}).then(res => {
				let data = res.data;
				this.addressInfo = data.address_info;
				this.addressId = data.address_info.id || '';
				this.lists = data.goodsBuy;
			})
		},
		async  bookOrders(){
			if(!this.addressId){
				this.$pop.msg('请选择收货地址!!!')
				return;
			}
			let data,_this = this;
			if(this.ids){
				data = {
					cartId: 0, 
					addressId: _this.addressId
				}
			}else{
				data = {
					productId: _this.lists[0].specId, 
					addressId: _this.addressId
				}
			}
			let res = await this.$http.request({
				url:this.$api.BookOrder,
				data:data,
				method:'POST'
			})
			this.$pop.msg('订单提交成功!!')
			uni.navigateBack({
				
			});
			
		},
		changeAdress() {
			uni.navigateTo({
				url: '/pages/mine/address/addressManger/addressManger'
			})
		}
	}
}
